.board_container {
    padding: 10px;
    box-sizing: border-box;
    width: 100%;
    height: 150px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-end;
}

.result {
    margin: 0;
    font-size: 20px;
    color: #fff;
}

.intermediate_data {
    margin: 0 0 5px;
    color: #728AB7;
    font-size: 16px;
}